import React from "react";
import request from '../api/api';
import Cookie from "js-cookie";
import Pubsub from 'pubsub-js';

class A extends React.Component {

  constructor(prors){
    super(prors)
    this.blur = this.blur.bind(this)
  }


  blur(event){
    var value = event.target.value;
    // console.log(value);
    //把username值传给Login组件 发布者
    Pubsub.publish("value",value);
  }
  render() {
    return (
      <div>
        <fieldset className="form-group">
          <input
            className="form-control form-control-lg"
            type="text"
            placeholder="Your Name"
            onBlur={this.blur}
          />
        </fieldset>
      </div>
    );
  }
}
class Login extends React.Component {
  constructor(props) {
    super(props);
    //为事件函数绑定this
    this.onsubmit = this.onsubmit.bind(this);
    this.onblur1 = this.onblur1.bind(this);
    this.onblur2 = this.onblur2.bind(this);
    //把表单中的input数据实现成响应式
    this.state = {
        username : "",
        email : "jason@qq.com",
        password : "123"
    }
  }

  componentDidMount() {
    // console.log("-----", this.props);
    // request({
    //      method:"POST",
    //      url:"/api/users/login",
    //      data:{
    //        user:{
    //         "email": "1573515091@qq.com",
    //         "password": "jason19830718"
    //        }
    //      }
    // }).then(res=>{console.log(res)})

        Pubsub.subscribe("value",(msg,data)=>{
             this.setState({
                username : data
             })
        })
     };
     
    onsubmit(event){
      //屏蔽form表单的提交事件
      event.preventDefault()
      // console.log('----');
      let user = {
           username : this.state.username?this.state.username:"",
           email : this.state.email,
           password : this.state.password
      }
      request({
             method:"POST",
             url:this.state.username?"/api/users":"/api/users/login",
             data:{
               user:user
             }
      }).then(res=>{
        console.log(res);
        // console.log(res.data.user.token);
        //将token存储到Cookie中
        Cookie.set("auth",res.data.user.token);
        // console.log('--',this);
        //跳转路由
        this.props.history.push("/");
        //发布
        Pubsub.publish("auth",res.data.user.token);
      })
    }

    onblur1(event){
      // console.log(event.target.value);
       this.setState({
           email : event.target.value
       })
    }
    onblur2(event){
      // console.log(event.target.value);
      this.setState({
        password : event.target.value
    })
    }

  isLogin() {
    //判断是注册还是登陆  register   Sign up  login   Sign in
    return this.props.match.path === "/login";
  }

  render() {
    // console.log(this.state);
    return (
        <div className="auth-page">
          <div className="container page">
            <div className="row">
              <div className="col-md-6 offset-md-3 col-xs-12">
                <h1 className="text-xs-center">
                  {this.isLogin() ? "Sign in" : "Sign up"}
                </h1>
                <p className="text-xs-center">
                  <a href="">
                    {this.isLogin() ? "Need an account" : " Have an account"}
                  </a>
                </p>
                <ul className="error-messages">
                  <li>That email is already taken</li>
                </ul>
                <form onSubmit={this.onsubmit}>
                  {this.isLogin()?'':<A/>}
                  <fieldset className="form-group">
                    <input
                      className="form-control form-control-lg"
                      type="text"
                      placeholder="Email"
                      onBlur={this.onblur1}
                    />
                  </fieldset>
                  <fieldset className="form-group">
                    <input
                      className="form-control form-control-lg"
                      type="password"
                      placeholder="Password"
                      onBlur={this.onblur2}
                    />
                  </fieldset>
                  <button className="btn btn-lg btn-primary pull-xs-right">
                    {this.isLogin() ? " Sign in" : "  Sign up"}
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
    );
  }
}

export default Login;
