import React from "react";
import request  from "../api/api";
import { Link,NavLink } from "react-router-dom";
import { addFavorite, deleFavorite } from "../api/favorite";


class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {
        articles: [],
      },
      //接受每页数据的集合
      pages: [],
      activePage:0,
      offset:0,

      //数据在加载过程中点赞禁止
      favoritedisabled : false
    };
    this.changeClick = this.changeClick.bind(this) 
    this.fnFavorite = this.fnFavorite.bind(this)
  }

  //点赞
  async fnFavorite(data){


    await this.setState({   //异步
        favoritedisabled : true
      })
      console.log(data);//当前点击的单个文章
      // data.favorited true  已经点赞  false 未点赞
      if(data.favorited){
        //取消点赞的业务
       await deleFavorite(data).then((res) => { //异步
          console.log("取消",res);
        })
        data.favorited = false
        data.favoritesCount += -1;

         // console.log(this.state.data.articles.indexOf(data));
         let key = this.state.data.articles.indexOf(data);
         this.state.data.articles[key] = data;
         await this.setState({  //异步
           articles : this.state.data.articles
         })

      }
      else{
        //点赞的业务
        await addFavorite(data).then((res) => {  //异步
          console.log("添加",res);
        })

        data.favorited = true
        data.favoritesCount += 1;

        // console.log(this.state.data.articles.indexOf(data));
        let key = this.state.data.articles.indexOf(data);
        this.state.data.articles[key] = data;
        await this.setState({  //异步
          articles : this.state.data.articles
        })
      }

      await this.setState({
        favoritedisabled : false
      })

  }

  componentDidMount() {
    //普通的请求
    request({
      method: "GET",
      url: "/api/articles",
      params: {},
    }).then((res) => {
      console.log(res);
      var pages = [];
      for (var i = 0; i < res.data.articlesCount/20; i++) {
        pages.push(i);
      }
      this.setState({
        data: res.data,
        pages: pages,
      });
    });
  }

  changeClick = (id) => {
    console.log(id)

    request({
      method: "GET",
      url: "/api/articles",
      params: {
        offset: id
      },
    }).then((res) => {
      console.log(res);
      this.setState({
        activePage : id,
        data: res.data,
      });
    });
  }
  render() {
    return (
      <div className="home-page">
        <div className="banner">
          <div className="container">
            <h1 className="logo-font">conduit</h1>
            <p>A place to share your knowledge.</p>
          </div>
        </div>

        <div className="container page">
          <div className="row">
            <div className="col-md-9">
              <div className="feed-toggle">
                <ul className="nav nav-pills outline-active">
                  <li className="nav-item">
                    <a className="nav-link disabled" href="">
                      Your Feed
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link active" href="">
                      Global Feed
                    </a>
                  </li>
                </ul>
              </div>
              {this.state.data.articles.map((item, index) => {
                return (
                  <div className="article-preview" key={index}>
                    <div className="article-meta">
                      <a href="profile.html">
                        <img src={item.author.image} />
                      </a>
                      <div className="info">
                        <a href="" className="author">
                          {item.author.username}
                        </a>
                        <span className="date">{item.createdAt}</span>
                      </div>
                      <button className="btn btn-outline-primary btn-sm pull-xs-right"
                      onClick={()=>{
                          this.fnFavorite(item)
                      }} disabled = {this.state.favoritedisabled}>
                        <i className="ion-heart"></i> {item.favoritesCount}
                      </button>
                    </div>
                    <a href="" className="preview-link">
                      <h1>{item.title}</h1>
                      <p>{item.description}</p>
                      <span>Read more...</span>
                    </a>
                  </div>
                );
              })}

              <ul class="pagination">
                {this.state.pages.map((item, key) => {
                  return (
                    <li 
                      key={key} 
                      className={this.state.activePage == key ? "page-item active" : "page-item"}
                      onClick={() => {
                        this.changeClick(key)
                      }}
                      >
                      {
                      /* <NavLink className="page-link" to="">
                          {key+1}
                      </NavLink> */
                      }
                      <span className="page-link">{key+1}</span>
                    </li>
                  );
                })}
              </ul>
            </div>

            <div className="col-md-3">
              <div className="sidebar">
                <p>Popular Tags</p>

                <div className="tag-list">
                  <a href="" className="tag-pill tag-default">
                    programming
                  </a>
                  <a href="" className="tag-pill tag-default">
                    javascript
                  </a>
                  <a href="" className="tag-pill tag-default">
                    emberjs
                  </a>
                  <a href="" className="tag-pill tag-default">
                    angularjs
                  </a>
                  <a href="" className="tag-pill tag-default">
                    react
                  </a>
                  <a href="" className="tag-pill tag-default">
                    mean
                  </a>
                  <a href="" className="tag-pill tag-default">
                    node
                  </a>
                  <a href="" className="tag-pill tag-default">
                    rails
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
